import { isApiGraphQLConfig } from './internals/utils/runtimeTypeGuards/isApiGraphQLProviderConfig';
import { generateCustomQueriesProperty, generateCustomMutationsProperty, generateEnumsProperty, upgradeClientCancellation, } from './internals';
import { generateModelsProperty as generateModelsPropertyServer } from './internals/server';
// TODO: separate import path
export function addSchemaToClientWithInstance(client, params, getInternals) {
    const apiGraphqlConfig = params.config?.API?.GraphQL;
    if (isApiGraphQLConfig(apiGraphqlConfig)) {
        upgradeClientCancellation(client);
        client.models = generateModelsPropertyServer(client, params, getInternals);
        client.enums = generateEnumsProperty(apiGraphqlConfig);
        client.queries = generateCustomQueriesProperty(client, apiGraphqlConfig, getInternals);
        client.mutations = generateCustomMutationsProperty(client, apiGraphqlConfig, getInternals);
    }
    return client;
}
